body {
  background-color: rgb(255, 249, 240);
}
.Top {
  display: flex;
  align-items: center;
  justify-self: flex-start;
  padding: 0 15px;
}

.para {
  font-size: 35px;
  font-weight: bold;
  padding: 30px;
  display: flex;
  justify-content: center;
}
.name {
  padding: 15px 15px;
  border: none;
  margin: 5px 20px;
  background-color: rgb(180, 143, 143);
  color: white;
  outline: none;
  resize: none;
  width: 50vh;
}
.submit {
  width: 80px;
  margin: 15px 15px;
  background-color: rgb(202, 190, 176);
  border: none;
  border-radius: 30px;
  padding: 10px;
  cursor: pointer;
}
